<template>
<div class="order">
    <!-- 头部 -->
    <Head :agood='1' :bgood='bgood' :cgood='3'></Head>
    <!-- 内容区域的头部 -->
    <div class="content">
        <div class="header-a" @click="login">
            <div class="header-b">
                <svg  v-if="image==null" data-v-0fc4ab7a="" class="privateImage-svg"><use data-v-0fc4ab7a="" xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#avatar-default">
                    <svg viewBox="0 0 122 122" id="avatar-default" width="100%" height="100%"><path fill="#DCDCDC" fill-rule="evenodd" d="M61 121.5c33.413 0 60.5-27.087 60.5-60.5S94.413.5 61 .5.5 27.587.5 61s27.087 60.5 60.5 60.5zm12.526-45.806c-.019 3.316-.108 6.052.237 9.825 3.286 8.749 18.816 9.407 28.468 17.891-1.833 1.998-6.768 6.788-15 10.848-7.02 3.463-16.838 6.416-24.831 6.416-17.366 0-32.764-7.149-42.919-17.264 9.713-8.407 25.49-9.173 28.769-17.891.345-3.773.258-6.509.24-9.825l-.004-.002c-1.903-.985-5.438-7.268-6.01-12.571-1.492-.12-3.843-1.561-4.534-7.247-.37-3.053 1.107-4.77 2.004-5.31-5.046-19.212 1.507-33.16 20.749-34.406 5.753 0 10.18 1.52 11.909 4.523 15.35 2.702 11.756 22.658 9.328 29.882.899.54 2.376 2.258 2.004 5.31-.689 5.687-3.042 7.127-4.534 7.248-.575 5.305-3.25 10.82-5.873 12.57l-.003.003zM61 120.5C28.14 120.5 1.5 93.86 1.5 61S28.14 1.5 61 1.5s59.5 26.64 59.5 59.5-26.64 59.5-59.5 59.5z"></path></svg>
                </use></svg>
                <img :src="'//elm.cangdu.org/img/'+image" alt="" v-else />
                <div class="header-c">
                    <p v-if="show==false">登录/注册</p>
                    <p v-else>{{name}}</p>
                    <p><van-icon name="graphic"/>暂无绑定手机号</p>
                </div>
                <div class="el-icon-arrow-right" ></div>

            </div>
        </div>
        <!-- 余额，特惠 -->
        <div class="head-a">
            <div>
                <p><span class="span1">0.00</span>元</p>
                <p>我的余额</p>
            </div>
            <div class="center">
                <p><span class="span2">0</span>个</p>
                <p>我的优惠</p>
            </div>
            <div>
                <p><span class="span3">0</span>分</p>
                <p>我的积分</p>
            </div>
        </div>
        <!--  -->
        <div class="card">
            <div>
                <div>1</div>
                <div>22<span>33</span></div>
            </div>
            <div>
                <div>2</div>
                <div>44<span>355</span></div>
            </div>
            <div>
                <div>3</div>
                <div>466<span>6665</span></div>
            </div>
        </div>
        <!--  -->
        <div class="card1">
            <div>
                <div>1</div>
                <div>22<span>33</span></div>
            </div>
            <div>
                <div>2</div>
                <div>44<span>355</span></div>
            </div>
        </div>
    </div>
    <bottom></bottom>
</div>    
</template>
<script>
import bottom from '@/components/bottom'
import Head from '@/components/head/Head'
export default {
    name:'my',
    components:{Head,bottom},
    data(){
        return{
            bgood:'我的',
            show:false,
            name:'',
            image:''
        }
    },
    beforeMount(){
        this.image=this.$store.state.login.headPhoto
        // console.log(this.$store.state.login.headPhoto)
    },
    //显示用户名
    created(){
        if(localStorage.getItem('vuex')){
            this.show=true
            this.name=JSON.parse(localStorage.getItem('vuex')).login.user
            // console.log(this.name)
        }
    },
    methods:{
        //如果没登陆，跳登陆页面
        login(){
            if(localStorage.getItem('vuex')){
                this.$router.push('/logout')
            }else{
                this.$router.push('/login')
            }
            
        }
    }
}
</script>
<style lang='scss'>
.content{
    width: 100%;
    margin-top: .91rem;
    .header-a{
        width: 100%;
        height: 1.9rem;
        background: #3190e8;
        .header-b{
            width: 92%;
            height: 100%;
            margin: 0 auto;
            display: flex;
            align-items: center;
            .privateImage-svg{
                width: 1.5rem;
                height: 1.5rem;
            }
            img{
                width: 1.5rem;
                height: 1.5rem;
                border-radius: 50%;
            }
            .header-c{
                margin-left: .3rem;
                p{
                     width: 3.2rem;
                    font-size: .4rem;
                    color:white;
                    padding: 0;
                    margin: 0;
                }
            }
            .el-icon-arrow-right{
                color: white;
                margin-left: 1.2rem;
            }
        }
    }
    .head-a{
        background: white;
        width: 100%;
        height: 1.6rem;
        display: flex;
        // justify-content: center;
        align-items: center;
        div{
            width: 33%;
            p{
                text-align: center;
                padding: 0;
                margin: 0;
                font-size: .36rem;
            }
        }
        .center{
            border-left: 0.01rem solid #f5f5f5; 
             border-right: 0.01rem solid #f5f5f5; 
        }
    }
}
.span1{
    font-size: .5rem;
    color: #ff9900;
}                 
.span2{
    font-size: .5rem;
    color: #ff5f3e;
}                  
.span3{
    font-size: .5rem; color: #6ac20b;
}
.card{
    width: 100%;
    height: 3rem;
    background: white;
    margin-top: .25rem;
    
}                      
.card>div{
    height: 33%;
    font-size: .32rem;
    display: flex;
    align-items: center;
}                  


.card1{
    width: 100%;
    height:2rem;
    background: white;
    margin-top: .25rem;
}
.card1>div{
    height: 50%;
    font-size: .32rem;
    display: flex;
    align-items: center;
}                      
               
</style>


